import React, { useEffect, useState } from 'react'
import ImageZoom from 'react-medium-image-zoom'
import './TestView.css'

/**
* @author
* @function TestView
**/

const TestView = ( {image, original, random} ) => {

    const [isZoomed, setZoomed] = useState(false)

    useEffect(() => {

    },[])

    const className = `col-6 col-md-6 col-lg-${random} aos-init aos-animate`

    const aClass = isZoomed ? "" : "d-block photo-item"

  return(
        <div 
        className={className}  
        data-aos="fade-up" 
        data-aos-delay="100"
        >

        <a 
        className={aClass}
        onClick={() => setZoomed(!isZoomed)}
        >
        <ImageZoom 
            isZoomed={isZoomed}
            image={{
                src: image,
                alt: "",
                className: 'img-fluid'
            }}
            zoomImage={{
                src: original,
                alt: ""
            }}
            defaultStyles={{
                
                
            }}
            overlayBgColor='rgba(0,0,0,0.95)'
        
            />
                <div className="photo-text-more">
                    <h3 className="heading">ABRIR</h3>
                </div>
            </a>
        </div>
   )

 }

export default TestView