import './App.css';
import { useEffect, useState } from 'react';
import TestView from './views/TestView/TestView'
import SiteMobileMenu from './views/SiteMobileMenu'
import Menu from './views/menu/Menu'
import imagesHuasteca from './imagesCompressedHuasteca'
import originalsHuasteca from './imagesOriginalHuasteca'
import imagesRecepcion from './imagesCompressedRecepcion'
import originalRecepcion from './imagesOriginalRecepcion'




function App() {

  const [images, setImages] = useState(imagesHuasteca)
  const [originals, setOriginals] = useState(originalsHuasteca)
  const [place, setPlace] = useState('huasteca')
  useEffect(() => {
    let ref = window.location.href.split("/")
    let location = ref[ref.length - 1]
    if (location === 'huasteca') {
      setImages(imagesHuasteca)
      setOriginals(originalsHuasteca)
    } 
    if (location === 'recepcion') {
      setImages(imagesRecepcion)
      setOriginals(originalRecepcion)
    }
    setPlace('location')
  },[])

  //the arrangements of the image blocks
  const arrngements = {
    1:[4,8],
    2:[8,4],
    3:[6,6]
//    4:[4,4,4]
  }

  const changeImages = (bool) => { // the callback. Use a better name
    console.log(`Change images ${bool}`)
    if (bool) {
      setImages(imagesHuasteca)
      setOriginals(originalsHuasteca)
    } else {
      setImages(imagesRecepcion)
      setOriginals(originalRecepcion)
    }
  };

  let n = 1
  let nbefore = 1

  const getRandomInt = (min, max) => {
    min = Math.ceil(min);
    max = Math.floor(max);
    return Math.floor(Math.random() * (max - min + 1)) + min;
  }

  return (
    <div>
      <div className="site-wrap">
        <SiteMobileMenu/>
        <Menu
          changeImages = {changeImages}
        />
      <main className="main-content">
          <div className="container-fluid photos">
            <div className="row align-items-stretch">
              {images.map( (image, i) => { 

                  //if i is pair get a new random arrangement
                  if (i % 2 == 0) {
                    n = getRandomInt(1,3)
                    while (n === nbefore) {
                      n = getRandomInt(1,3)
                    }
                  }
                  nbefore = n
                return (<TestView
                  image={image}
                  original = {originals[i]}
                  key={i}
                  random = {arrngements[n][i%2]} //i%2 will produce the position of 0 and then 1
                />)
              })}
            </div>
          </div>
      </main>
      </div>
    </div>
  );
}

export default App;
